.wgs-header-links{position:fixed;top:0;left:0;width:100%;display:flex;background:linear-gradient(90deg, #18aee8, #13c6a3);z-index:2}.wgs-header-links .textlink{padding:1rem;cursor:pointer;color:white}.wgs-home-link{height:36px;padding:8px}@supports ((-webkit-backdrop-filter: blur(8px)) or (backdrop-filter: blur(8px))){.wgs-header-links{background:linear-gradient(90deg, rgba(24,174,232,0.5), rgba(19,198,163,0.5));backdrop-filter:blur(8px);-webkit-backdrop-filter:blur(8px)}}

