.wgs-section-title{font-size:3rem}.wgs-section-horizontal-row{box-sizing:border-box;position:relative;left:-1rem;white-space:nowrap;width:calc(100% + 2rem);overflow:auto;padding:1rem}.wgs-section-horizontal-row.wrap{white-space:normal;overflow:visible}.wgs-tile{display:inline-flex;flex-direction:row;width:384px;box-shadow:0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);color:black;background:white;transition:box-shadow 0.2s, -webkit-transform 0.2s;transition:transform 0.2s, box-shadow 0.2s;transition:transform 0.2s, box-shadow 0.2s, -webkit-transform 0.2s}.wgs-tile .wgs-tile-icon{width:128px;height:128px}.wgs-tile .wgs-tile-body{box-sizing:border-box;padding:1rem;width:256px;display:flex;flex-direction:column;white-space:normal}.wgs-tile .wgs-tile-body h2{margin:0}.wgs-tile:hover{-webkit-transform:translateY(-2px);transform:translateY(-2px);box-shadow:0 20px 25px -5px rgba(0,0,0,0.1),0 10px 10px -5px rgba(0,0,0,0.04)}

