.wgs-footer{position:relative;width:100%;background-color:#eee;display:flex;flex-direction:row;color:#222}.wgs-footer h3{font-size:1.5rem}.wgs-footer img{padding:2rem;width:192px;height:192px;flex-shrink:0}.wgs-footer .column{width:100%}

